head {
    background-color: lightseagreen
}
body {
    
}

.container {
    border: 1px solid green;
}

.row {
    text-align: center;
    height: 300;

}

.col {
    border: 3px dotted blue;
}

.main-table {
    vertical-align: middle;
    color: rgb(18, 156, 0);
}

.hello {
    padding: 40px;
    font-family: Arial;
    text-align: center;
}

.filter-unit {
    padding-top: 10%;
}

.filter-bar {
    display: block;
    width: 100%;
    background-color: green;
}

.search-button-col {
    padding: 10%;
}

.bTable {
}

.filter-by {
    text-align: left;
    color: red;
    padding: 0.5em;
}

.major-line {
    border: 2px solid black;
}

.minor-line {
    border: 0.5px dashed black;
}

.feature-buttons {
    color: black;
    width: 100%;
    border: 1px solid;
    background: #efefef;
    padding: 10px 20px;
    cursor: pointer;
    transition: 0.4s;
}

.feature-buttons:hover {
    border: 1px solid dodgerblue;
    background-color: dodgerblue;
    color: white;
}

.feature-buttons:active {
    background-color: darkblue;
    transform: translateY(4px); 
}

.last-line {
    border: 0.5px solid;
    text-align: center;
    width: auto;
}
